import React, { Component } from 'react';
import $ from 'jquery'
import logo from './logo.svg';
import Card from './Card';
import NoFound from './NoFound';
import './App.css';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group' // ES6
//var ReactCSSTransitionGroup = require('react-addons-css-transition-group') // ES5 with npm
//var ReactCSSTransitionGroup = React.addons.CSSTransitionGroup; // ES5 with react-with-addons.js

class App extends Component {
  constructor (props){
    super(props);
    this.state = {
      allData: [],
      data: [],
      isFound: true,
      currently: ""
    };
  }
  componentWillMount() {
    $.getJSON("https://raw.githubusercontent.com/nos-joel/fow/master/wheelers.json",
      (fetched_data) => {
        this.setState({ data: fetched_data.wheelers });
        console.log(this.state.data)
      }
    );
  }

componentDidMount(){
  $("#filter").click((e)=>{
    this.getFiltered();
  });

}
getAll()
{
  $.getJSON("https://raw.githubusercontent.com/nos-joel/fow/master/wheelers.json",
    (fetched_data) => {
      this.setState({ data: fetched_data.wheelers });
      this.setState({ isFound: true });
      this.setState({ allData: fetched_data.wheelers });
     //});
      console.log(this.state.data)
    }
  );
}
getLocation(location){
  let wanted = location; //"metro";
  this.setState({currently: wanted });
  let fetched = this.getByLocation(this.state.allData, wanted);
  if (fetched.length === 0)
  {
    this.setState({isFound: false});
  }
  else { this.setState({isFound: true}); }
  console.log("fetched is: ");
  console.log(fetched);
  this.setState({data: fetched})
  }

getByLocation (items, wanted){
    return items.filter((el) =>
      el.compass === wanted
    )
}
  render() {
  //  let results = this.getFiltered;
  let entries;
  let isFound = this.state.isFound;
  console.log(isFound);
  if (isFound) {
    entries = this.state.data.map((el) =>
    <Card key={el.key} url={el.imgurl} menu={el.menu} location={el.location}
      name={el.name} status={el.status}
    />
    );
  }
  else {
    entries = <p> {"  "}:( Sorry, but no FootTruck was found in the {this.state.currently} area!</p>;
  }

    return (
      <div>
        <div className="App">
          <div className="App-header">
            <img src={logo} className="App-logo" alt="logo" />
            <h2 className="App-intro">Food On Wheels PR</h2>
          </div>
        </div>

        <div className="ctrl-wrap">
          <div className="ctrl" onClick={() => this.getAll()} >All</div>
          <div className="ctrl" onClick={() => this.getLocation("metro")} >Metro</div>
          <div className="ctrl" onClick={() => this.getLocation("norte")} >Norte</div>
          <div className="ctrl" onClick={() => this.getLocation("sur")} >Sur</div>
          <div className="ctrl" onClick={() => this.getLocation("este")} >Este</div>
          <div className="ctrl" onClick={() => this.getLocation("oeste")} >Oeste</div>
        </div>

        <ReactCSSTransitionGroup
          transitionName="example"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={500}>
            {entries}
        </ReactCSSTransitionGroup>
      </div>
    );
  }
}

export default App;

